
.head__logo img,.good__logo img, .choice__logo img{
   animation: circling 10s infinite linear;
   rotate: 0;
}
.head-pic__frame-box i,.letstalk__content .letstalk__icon-1{
   scale: 0.5;
   opacity: 0;
   animation: shining 2s infinite linear;
}
.head-icon_1, .letstalk__content .letstalk__icon-2{
   scale: 0.5;
   opacity: 0;
   animation: shining 3s infinite linear 1s;
}
.head-icon_2, .letstalk__content .letstalk__icon-3{
   scale: 0.5;
   opacity: 0;
   animation: shining 2s infinite linear .5s;
}
.head-pic__frame{
   animation: gravitate 7s infinite linear;
}
.decors__item{
   transition: all 0.5s;
}
.decors__item:hover{
   scale: 1.2;
   z-index: 2;
}


@keyframes gravitate {
   50%{
      transform: translateX(30px) translateY(50px) rotate(-15deg);
   }
}
@keyframes circling {
   100%{
      rotate: 360deg;
   }
}
@keyframes shining {
   50%{
      opacity: 1;
   }
   100%{
      scale: 1;
      opacity: 0;
   }
}




